<template>
    <div> 
        <Head title="当前城市" :show="true" ></Head>                
        <van-index-bar>
            <div v-for="(py,i) in citylist" :key="i">
                <van-index-anchor :index="py.areaName" />
                <van-cell 
                    :title="city.cityName" 
                    v-for="(city,j) in py.city" 
                    :key="j" 
                    @click="selectcity(city)"
                />
            </div>    
        </van-index-bar>
                       
    </div>
</template>
<script>
export default {
    data(){
        return{
            citylist:[]
        }
    },
    methods:{
        selectcity(city){
            localStorage.setItem("city",JSON.stringify(city)); // vuex
          this.$router.push({name:"1"});
        }
    },
    mounted(){
        this.$axios.get("/benlai/IHome/SiteList?timestamp=1589190050412&_t=1589190050&webId=3&KeyWord=&Type=&szSpecialUrl=&szShareProcUrl=&url=")
        .then(res=>{
            this.citylist=res.data.data.list
        })
    }
}
</script>